<template>
	<view class="flex flex-direction bg-white">
		<!-- 判断是否登录 -->
		<is-login currentPage="index"></is-login>
		<!-- 导航栏 -->
		<cu-custom bgColor="bg-cyan" :isBack="preview" >
			<block slot="content">评价</block>
		</cu-custom>
		
		<uni-forms>
			<!-- 服务评分 -->
			<uni-forms-item name="ProductSn" >
				<view class="cu-form-group padding-left-xl solid-bottom" name="ProductSn">
					<text class="title text-bold">服务评分</text>
					<uni-rate value="5" allowHalf v-model="current.Grade"></uni-rate>
				</view>
			</uni-forms-item>
			<!-- 服务评价 -->
			<uni-forms-item name="ProductSn" >
				<view class="cu-form-group padding-left-xl solid-bottom" name="ProductSn">
					<textarea placeholder="本次服务是否满意,写下您的宝贵建议!"></textarea>
				</view>
			</uni-forms-item>
			<!-- 上传图片 -->
			<view class=" cu-bar  padding-sm ">
				<view class="action text-bold text-xl">
					上传图片
				</view>
				<view class="action">
					{{imgList.length}}/9
				</view>
			</view>
			<uni-form-item>
				<view class="cu-form-group padding-left-xl solid-bottom margin-top">
					<view class="grid col-4 grid-square flex-sub">
						<view class="bg-img" v-for="(item,index) in imgList" :key="index" @tap="ViewImage" :data-url="imgList[index]">
						 <image :src="imgList[index]" mode="aspectFill"></image>
							<view class="cu-tag bg-red" @tap.stop="DelImg" :data-index="index">
								<text class='cuIcon-close'></text>
							</view>
						</view>
						<view class="solids" @tap="ChooseImage" v-if="imgList.length<9">
							<text class='cuIcon-cameraadd'></text>
						</view>
					</view>
				</view>
			</uni-form-item>
		</uni-forms>
		<button class="cu-btn bg-cyan margin padding round" @click="submit">提交</button>
	</view>
</template>

<script>
	/**
	 * 工单评价
	 *  @author 陆彦捷
	 *  @date 2021.07.09 评价样式
	 *  @date 2021.07.23 工单评价实例化
	 *  @description  工单评价(注：不需要验证)
	 */
	var _self;
	export default {
		data() {
			return {
				// 与样式有关参数
				preview : false,		// 是否返回上一页
				// 与系统有关参数
				OrderId : 0 ,			// 工单id
				imgList:[],				// 上传图片
				current:{
					OrderId : 0 ,		// 工单编号
					Grade: 5 ,			// 评分（默认为5）
					Msg : '',			// 提示信息
					UserId : 0 ,		// 当前用户
					imgs : '',			// 图片组，用逗号隔开
				}
			}
		},
		onLoad:function(options){
			_self = this;
			this.getParams(options);
		},
		methods: {
			// ========================================= 初始化方法 =========================================================
			/** 初始化方法
			 * @param {Object} options
			 */
			getParams : function(options){
				this.preview = options.preview != 'false'? Boolean(options.preview) : false;
				// 工单id(暂时这么判断，非0则返回)
				this.OrderId = Boolean(options.OrderId) ? parseInt(options.OrderId) : 0 ;		// 工单Id
				if(!Boolean(this.OrderId)){
					uni.navigateBack();		// 返回上一页
				}
			},
			// ========================================= 监听事件 ===========================================================
			/**
			 * 选择图片
			 */
			ChooseImage : function() {
				uni.chooseImage({
					count: 9, //默认9
					sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
					sourceType: ['album'], //从相册选择
					success: (res) => {
						if (this.imgList.length != 0) {
							this.imgList = this.imgList.concat(res.tempFilePaths)
						} else {
							this.imgList = res.tempFilePaths;
						}
					}
				});
			},
			/** 查看图片
			 * @param {Object} e
			 */
			ViewImage : function (e) {
				uni.previewImage({
					urls: this.imgList,
					current: e.currentTarget.dataset.url
				});
			},
			/** 提交工单
			 * @param {Object} e
			 */
			submit: function(e){
				console.log("提交" , this.current);
				// console.log("图片", this.imgList.toString());
				// uni.showToast({
				// 	title:'提交成功！',
				// 	duration:2000
				// });
				// setTimeout(function(){
				// 	uni.navigateTo({
				// 		url:'/pages/index/index?PageCur=repair_order'
				// 	})
				// },2000);
			},
			// =============================================================================================================
		}
	}
</script>

